<template>
  <v-app style="background:white" dark>
    <v-app-bar app color="white" elevation="2" v-if="$route.name != 'Login'">
      <v-container fluid>
        <v-layout justify-center align-center>
          <img
            @click="$router.push({ name: 'Login' })"
            class="ma-2 pa-n4"
            src="./../public/img/Logo/Logo-dmt.jpg"
          />

          <v-btn
            v-if="
              $route.name == 'Userinfo' ||
                $route.name == 'Testform' ||
                $route.name == 'Results'
            "
            id="backtohome"
            color="white"
            class="primary--text"
            fab
            depressed
            rounded
            @click="backtohome()"
          >
            <v-icon color="primary">mdi-home</v-icon>
          </v-btn>
        </v-layout>
      </v-container>

      <v-spacer></v-spacer>
    </v-app-bar>

    <v-main>
      <router-view
        @userLogin="userLogin()"
        :globalData="globalData"
        @googleUserData="data => (globalData.googleUser = data)"
        @testCreated="testCreated"
      ></router-view>
    </v-main>

    <!--  Navbar unten -->
    <v-footer
      color="white"
      elevation="2"
      fixed
      padless
      v-if="
        $route.name == 'Home' ||
          $route.name == 'Userinfo' ||
          $route.name == 'Login' ||
          $route.name == 'Results'
      "
    >
      <v-container fluid class="text-center">
        <h5 v-if="$route.name == 'Login'" class="text-center black--text">
          Powered by mb-mediasports ©
        </h5>
        <h5 v-if="$route.name != 'Login'" class="text-center black--text">
          Motor Health Promotion ©
        </h5>
      </v-container>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  components: {},
  data() {
    return {
      globalData: {
      assistant_code: ""
      },
    };
  },
  methods: {
    backtohome() {
      this.$router.push({ name: 'Home' });
    },
    testCreated(t) {
      console.log(t);
    },
  },
};
</script>

<style>
.v-bottom-navigation--fixed {
  position: fixed !important;
}

.v-item-group.v-bottom-navigation .v-btn {
  height: inherit !important;
}

img {
  margin-top: -0.5rem;
  max-height: 2.5rem;
}

/* #Nav {
  border-top: #ff6600 1.5px solid !important;
} */

#backtohome {
  position: absolute;
  top: 0;
  left: 0;
}
</style>
