<template>
  <v-container fluid pb-10>
    <v-text-field
      placeholder="Assistant Code"
      v-model="globalData.assistant_code"
      class="centered-input mt-4 pt-0 mb-2"
      readonly
      hide-details
      single-line
      style="width: 100%;"
    ></v-text-field>

    <v-text-field
      placeholder="Laibchen"
      v-model="testDaten.laibchen"
      class="centereed-input mt-2 pt-0 mb-4"
      readonly
      hide-details
      single-line
      style="width: 100%;"
    ></v-text-field>

    <v-carousel
      interval="60000"
      hide-delimiters
      v-model="slide"
      :show-arrows="false"
      height="auto"
      ma-10
    >
      <v-form>
        <v-carousel-item>
          <v-container>
            <h3 style="padding-bottom: 4%;" class="black--text">20m-Sprint</h3>
            <v-layout wrap>
              <v-flex>
                <v-text-field
                  label="Versuch 1"
                  suffix="sek"
                  outlined
                  class="cinput mt-4 pt-0 mb-2"
                  v-model="testDaten.sprint_D1"
                  :rules="sprint_rules"
                  light
                ></v-text-field>

                <v-text-field
                  label="Versuch 2"
                  suffix="sek"
                  outlined
                  class="cinput mt-4 pt-0 mb-2"
                  v-model="testDaten.sprint_D2"
                  :rules="sprint_rules"
                  light
                ></v-text-field>

                <v-alert
                  class="text-center"
                  style="width: 33%; margin-left: 33.333%;"
                  dense
                  color="primary"
                >20m</v-alert>

                <br />
                <p class="black--text text-center">
                  Externer Starter. Zeitnahme auf Höhe der Linie!
                  <br />Einheit:
                  <b>Min,Sec (00,00)</b>
                </p>
              </v-flex>
            </v-layout>
          </v-container>
        </v-carousel-item>

        <v-carousel-item>
          <v-container>
            <h3 style="padding-bottom: 1%;" class="black--text">Balancieren - Rückwärts</h3>
            <v-layout wrap>
              <v-flex>
                <h4 class="black--text">6 cm:</h4>
                <v-row>
                  <v-col cols="6">
                    <v-text-field
                      label="Versuch 1"
                      placeholder="Versuch 1"
                      suffix="Anz"
                      outlined
                      class="cinput mt-0 pt-0"
                      v-model="testDaten.Bal_D1_6"
                      :rules="Bal_rules"
                      light
                    ></v-text-field>
                  </v-col>

                  <v-col cols="6">
                    <v-text-field
                      label="Versuch 2"
                      suffix="Anz"
                      outlined
                      class="cinput mt-0 pt-0"
                      v-model="testDaten.Bal_D2_6"
                      :rules="Bal_rules"
                      light
                    ></v-text-field>
                  </v-col>
                </v-row>

                <h4 class="black--text">4,5 cm:</h4>

                <v-row>
                  <v-col cols="6">
                    <v-text-field
                      label="Versuch 1"
                      suffix="Anz"
                      outlined
                      class="cinput mt-0 pt-0"
                      v-model="testDaten.Bal_D1_4_5"
                      :rules="Bal_rules"
                      light
                    ></v-text-field>
                  </v-col>

                  <v-col cols="6">
                    <v-text-field
                      label="Versuch 2"
                      suffix="Anz"
                      outlined
                      class="cinput mt-0 pt-0"
                      v-model="testDaten.Bal_D2_4_5"
                      :rules="Bal_rules"
                      light
                    ></v-text-field>
                  </v-col>
                </v-row>

                <h4 class="black--text">3 cm:</h4>
                <v-row>
                  <v-col cols="6">
                    <v-text-field
                      label="Versuch 1"
                      suffix="Anz"
                      outlined
                      class="cinput mt-0 pt-0 mb-2"
                      v-model="testDaten.Bal_D1_3"
                      :rules="Bal_rules"
                      light
                    ></v-text-field>
                  </v-col>

                  <v-col cols="6">
                    <v-text-field
                      label="Versuch 2"
                      suffix="Anz"
                      outlined
                      class="cinput mt-0 pt-0 mb-2"
                      v-model="testDaten.Bal_D2_3"
                      :rules="Bal_rules"
                      light
                    ></v-text-field>
                  </v-col>
                </v-row>

                <!-- <v-alert
                    class="text-center"
                    style="width: 33%; margin-left: 33.333%;"
                    dense
                    color="primary"
                >BalR</v-alert>-->
                <v-row>
                  <p class="black--text text-center" pb-10>
                    Der 1.Schritt zählt nicht! Schritte werden gezählt, bis es
                    zur Bodenberührung kommt oder 8 Punkte (
                    <b>Schritte</b>) erreicht sind.
                  </p>
                </v-row>
              </v-flex>
            </v-layout>
          </v-container>
        </v-carousel-item>

        <v-carousel-item>
          <v-container>
            <h3 style="padding-bottom: 4%;" class="black--text">Seitliches Hin- und Herspringen</h3>
            <v-layout wrap>
              <v-flex>
                <v-text-field
                  label="Versuch 1"
                  suffix="Anz"
                  outlined
                  class="cinput mt-4 pt-0 mb-2"
                  v-model="testDaten.SHH_D1"
                  :rules="SHH_rules"
                  light
                ></v-text-field>

                <v-text-field
                  label="Versuch 2"
                  suffix="Anz"
                  outlined
                  class="cinput mt-4 pt-0 mb-2"
                  v-model="testDaten.SHH_D2"
                  :rules="SHH_rules"
                  light
                ></v-text-field>

                <v-alert
                  class="text-center"
                  style="width: 33%; margin-left: 33.333%;"
                  dense
                  color="primary"
                >SHH</v-alert>

                <br />
                <p class="black--text text-center">
                  Höchste Konzentration beim Zählen und gleichzeitig Uhr im
                  Blick halten!
                  <br />Einheit:
                  <b>Anzahl</b>
                </p>
              </v-flex>
            </v-layout>
          </v-container>
        </v-carousel-item>

        <v-carousel-item>
          <v-container>
            <h3 style="padding-bottom: 4%;" class="black--text">Rumpfbeuge</h3>
            <v-layout wrap>
              <v-flex>
                <v-text-field
                  label="Versuch 1"
                  suffix="cm"
                  outlined
                  class="cinput mt-4 pt-0 mb-2"
                  v-model="testDaten.RB_D1"
                  :rules="RB_rules"
                  light
                ></v-text-field>

                <v-text-field
                  label="Versuch 2"
                  suffix="cm"
                  outlined
                  class="cinput mt-4 pt-0 mb-2"
                  v-model="testDaten.RB_D2"
                  :rules="RB_rules"
                  light
                ></v-text-field>

                <v-alert
                  class="text-center"
                  style="width: 33%; margin-left: 33.333%;"
                  dense
                  color="primary"
                >RB</v-alert>

                <br />
                <p class="black--text text-center">
                  Kniegelenke nicht beugen! Nach dem ersten Versuch kurz
                  aufrichten, langsam und ohne Schwung durchführen!
                  <br />Einheit:
                  <b>cm</b> mit Komma
                </p>
              </v-flex>
            </v-layout>
          </v-container>
        </v-carousel-item>

        <v-carousel-item>
          <v-container>
            <h3 style="padding-bottom: 4%;" class="black--text">Liegestütz</h3>
            <v-layout wrap>
              <v-flex>
                <v-text-field
                  label="Versuch 1"
                  suffix="Anz"
                  outlined
                  class="cinput mt-4 pt-0 mb-2"
                  v-model="testDaten.LS_Anzahl"
                  :rules="LS_rules"
                  light
                ></v-text-field>

                <v-alert
                  class="text-center"
                  style="width: 33%; margin-left: 33.333%;"
                  dense
                  color="primary"
                >LS</v-alert>

                <br />
                <p class="black--text text-center">
                  Nur korrekte Liegestütze zählen.
                  <br />Einheit:
                  <b>Anzahl</b> in 40 sek
                </p>
              </v-flex>
            </v-layout>
          </v-container>
        </v-carousel-item>

        <v-carousel-item>
          <v-container>
            <h3 style="padding-bottom: 4%;" class="black--text">Sit-Ups</h3>
            <v-layout wrap>
              <v-flex>
                <v-text-field
                  label="Versuch 1"
                  suffix="Anz"
                  outlined
                  class="cinput mt-4 pt-0 mb-2"
                  v-model="testDaten.SU_Anzahl"
                  :rules="SU_rules"
                  light
                ></v-text-field>

                <v-alert
                  class="text-center"
                  style="width: 33%; margin-left: 33.333%;"
                  dense
                  color="primary"
                >SitUp</v-alert>

                <br />
                <p class="black--text text-center">
                  Beim Ablegen müssen die Schulterblätter den Boden berühren,
                  beim Aufrollen berühren die Ellenbogen die Knie.
                  <br />Einheit:
                  <b>Anzahl</b> in 40 sec
                </p>
              </v-flex>
            </v-layout>
          </v-container>
        </v-carousel-item>

        <v-carousel-item>
          <v-container>
            <h3 style="padding-bottom: 4%;" class="black--text">Standweitsprung</h3>
            <v-layout wrap>
              <v-flex>
                <v-text-field
                  label="Versuch 1"
                  suffix="cm"
                  outlined
                  class="cinput mt-4 pt-0 mb-2"
                  v-model="testDaten.SW_D1"
                  :rules="SW_rules"
                  light
                ></v-text-field>

                <v-text-field
                  label="Versuch 2"
                  suffix="cm"
                  outlined
                  class="cinput mt-4 pt-0 mb-2"
                  v-model="testDaten.SW_D2"
                  :rules="SW_rules"
                  light
                ></v-text-field>

                <v-alert
                  class="text-center"
                  style="width: 33%; margin-left: 33.333%;"
                  dense
                  color="primary"
                >SW</v-alert>

                <br />
                <p class="black--text text-center">
                  So weit wie möglich springen! Nach hinten greifen oder fallen
                  zählt nicht!
                  <br />Einheit:
                  <b>cm</b>
                </p>
              </v-flex>
            </v-layout>
          </v-container>
        </v-carousel-item>

        <v-carousel-item>
          <v-container>
            <h3 style="padding-bottom: 4%;" class="black--text">6-Minuten-Lauf</h3>
            <v-layout wrap>
              <v-flex>
                <p class="black--text">Runden:</p>
                <v-text-field
                  class="cinput mt-4 pt-0 mb-2"
                  outlined
                  single-line
                  light
                  placeholder="Runden"
                  v-model="testDaten.ausdauer"
                  :rules="ausdauer_rules"
                >
                  <template slot="append">
                    <v-btn
                      small
                      outlined
                      style="margin-bottom: 33%;
                        color: rgba(0, 0, 0, 0.60);"
                      @click="removeRound"
                      class="pa-2"
                    >
                      <v-icon>mdi-minus</v-icon>
                    </v-btn>
                  </template>
                  <template slot="prepend-inner">
                    <v-btn
                      small
                      outlined
                      style="margin-bottom: 33%;
                        color: rgba(0, 0, 0, 0.60);"
                      @click="addRound"
                    >
                      <v-icon>mdi-plus</v-icon>
                    </v-btn>
                  </template>
                </v-text-field>

                <p class="black--text">Rest-Meter:</p>
                <v-text-field
                  outlined
                  light
                  suffix="m"
                  class="cinput mt-4 pt-0 mb-2"
                  v-model="testDaten.ausdauer_rest"
                  :rules="ausdauer_rest_rules"
                ></v-text-field>
                <v-alert
                  class="text-center"
                  style="width: 33%; margin-left: 33.333%;"
                  dense
                  color="primary"
                >6Min</v-alert>

                <br />
                <p class="black--text text-center">
                  Vorher Schnürsenkel kontrollieren! Kinder & Jugendliche
                  motivieren!
                  <br />Einheit:
                  <b>Anzahl</b> der Runden & Restrecke in
                  <b>Meter</b>.
                </p>
              </v-flex>
            </v-layout>
          </v-container>
        </v-carousel-item>

        <v-carousel-item>
          <v-container>
            <h3 class="black--text">Daten</h3>

            <v-simple-table light class="mb-10">
              <template v-slot:default>
                <tbody>
                  <tr>
                    <td>Laibchen</td>
                    <td></td>
                    <td>{{ testDaten.laibchen }}</td>
                  </tr>
                  <tr>
                    <td>Geschlecht</td>
                    <td></td>

                    <td>{{ testDaten.gender }}</td>
                  </tr>
                  <tr>
                    <td>Geburtstag</td>
                    <td></td>

                    <td>{{ testDaten.date_birth }}</td>
                  </tr>
                  <tr>
                    <td>Größe</td>
                    <td></td>
                    <td>{{ testDaten.groeße }}</td>
                  </tr>
                  <tr>
                    <td>Gewicht</td>
                    <td></td>
                    <td>{{ testDaten.gewicht }}</td>
                  </tr>
                  <tr>
                    <td>Sitzhöhe</td>
                    <td></td>
                    <td>{{ testDaten.sitzhoehe }}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
            <h3 class="black--text">Übungen</h3>
            <v-simple-table light>
              <template v-slot:default>
                <tbody>
                  <tr>
                    <td>
                      <b>20m-Sprint</b>
                    </td>
                    <td width="40%">V1</td>
                    <td width="52%">{{ testDaten.sprint_D1 }} sek</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>V2</td>
                    <td>{{ testDaten.sprint_D2 }} sek</td>
                  </tr>

                  <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>

                  <tr>
                    <td>
                      <b>Balancieren-Rückwärts</b>
                    </td>
                    <td>V1-6</td>
                    <td>{{ testDaten.Bal_D1_6 }}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>V2-6</td>
                    <td>{{ testDaten.Bal_D2_6 }}</td>
                  </tr>

                  <tr>
                    <td></td>
                    <td>V1-4,5</td>
                    <td>{{ testDaten.Bal_D1_4_5 }}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>V2-4,5</td>
                    <td>{{ testDaten.Bal_D2_4_5 }}</td>
                  </tr>

                  <tr>
                    <td></td>
                    <td>V1-3</td>
                    <td>{{ testDaten.Bal_D1_3 }}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>V2-3</td>
                    <td>{{ testDaten.Bal_D2_3 }}</td>
                  </tr>

                  <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>
                  <tr>
                    <td>
                      <b>Seiliches Hin- und Herspringen</b>
                    </td>
                    <td>V1</td>
                    <td>{{ testDaten.SHH_D1 }}</td>
                  </tr>

                  <tr>
                    <td></td>
                    <td>V2</td>
                    <td>{{ testDaten.SHH_D2 }}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>

                  <tr>
                    <td>
                      <b>Rumpfbeuge</b>
                    </td>
                    <td>V1</td>
                    <td>{{ testDaten.RB_D1 }} cm</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>V2</td>
                    <td>{{ testDaten.RB_D2 }} cm</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>

                  <tr>
                    <td>
                      <b>Liegestütze</b>
                    </td>
                    <td></td>
                    <td>{{ testDaten.LS_Anzahl }}</td>
                  </tr>

                  <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>

                  <tr>
                    <td>
                      <b>Situps</b>
                    </td>
                    <td></td>
                    <td>{{ testDaten.SU_Anzahl }}</td>
                  </tr>

                  <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>

                  <tr>
                    <td>
                      <b>Standweitsprung</b>
                    </td>
                    <td>V1</td>
                    <td>{{ testDaten.SW_D1 }} cm</td>
                  </tr>

                  <tr>
                    <td></td>
                    <td>V2</td>
                    <td>{{ testDaten.SW_D2 }} cm</td>
                  </tr>

                  <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>

                  <tr>
                    <td>
                      <b>6-Minuten-Lauf</b>
                    </td>
                    <td>Runden</td>
                    <td>{{ testDaten.ausdauer }}</td>
                  </tr>

                  <tr>
                    <td></td>
                    <td>Rest-Meter</td>
                    <td>{{ testDaten.ausdauer_rest }} m</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-container>
        </v-carousel-item>
      </v-form>
    </v-carousel>
    <v-footer fixed padless color="transparent">
      <v-container fluid d-flex justify-center align-center pa-0 ma-2>
        <v-btn @click="slide--" color="primary" dark class="ml-1 mr-1">
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>

        <v-btn color="secondary" class="white--text" @click="openD" width="60%">
          <span>Speichern</span>
        </v-btn>

        <v-btn @click="slide++" color="primary" dark class="ml-1 mr-1">
          <v-icon>mdi-arrow-right</v-icon>
        </v-btn>
      </v-container>
    </v-footer>

    <div class="text-center">
      <v-dialog class="mx-auto" v-model="dialog" width="500">
        <v-card
          style="border-style: solid; border-color:#1F497D; margin-top:4%;"
          class="text-center"
        >
          <v-card-text></v-card-text>
          <p
            style="font-family: calibri; font-size: 110%;"
            class="primary--text"
          >Möchtest du den Test beenden?</p>

          <v-container class="text-xs-center">
            <v-card-actions id="act">
              <v-layout row>
                <v-flex justify-center>
                  <v-btn dark color="primary" @click="done">
                    <v-icon dark color="white">mdi-check</v-icon>
                  </v-btn>

                  <v-btn style="margin-left: 30%;" dark color="primary" @click="dialog = false">
                    <v-icon dark color="white">mdi-close</v-icon>
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-card-actions>
          </v-container>
        </v-card>
      </v-dialog>
    </div>
  </v-container>
</template>

<script>
import moment from "moment";

export default {
  props: {
    globalData: {
      type: Object
    },
  },
  data() {
    return {
      slide: 0,
      dialog: false,
      klassen: ["5AHITN", "5BHITM", "5CHITM"],
      testDaten: {
        laibchen: "21",
        date_birth: "07/03/2003",
        age: null,
        radios: null,
        menu: null,
        date_test: null,
        gender: "Weiblich",
        gewicht: 49,
        groeße: 153,
        sitzhoehe: 41,
        //Sprint
        sprint_D1: 5.9,
        sprint_D2: 6.47,
        //Gleichgewicht
        Bal_D1_6: 7,
        Bal_D2_6: 7,
        Bal_D1_4_5: 4,
        Bal_D2_4_5: 5,
        Bal_D1_3: 3,
        Bal_D2_3: 3,
        //Hinundher
        SHH_D1: 59,
        SHH_D2: 40,
        //Rumpfbeuge
        RB_D1: -43,
        RB_D2: 43,
        //Liegestütze
        LS_Anzahl: 28,
        //Situps
        SU_Anzahl: 99,
        //Standweitsprung
        SW_D1: 793,
        SW_D2: 345,
        //Ausdauer
        ausdauer: 1,
        ausdauer_rest: 0,
      },
      ergebnisse: {
        //Punkteergebnisse
        erg_sprint: null,
        erg_Bal: null,
        erg_SHH: null,
        erg_RB: null,
        erg_LS: null,
        erg_SU: null,
        erg_SW: null,
        erg_ausdauer: null,
      },
      sprint_rules: [(v) => (v >= 2.0 && v <= 10.0) || "irreguläre Sprintzeit"],
      Bal_rules: [
        (v) => !!v || "Bitte Wert eingeben!",
        (v) => (v >= 0 && v <= 8) || "irregulärer Balancierwert",
      ],
      SHH_rules: [
        (v) => !!v || "Bitte Wert eingeben",
        (v) => (v >= 0 && v <= 60) || "irregulärer Koordinationswert",
      ],
      RB_rules: [
        (v) => !!v || "Bitte Wert eingeben",
        (v) => (v >= -50 && v <= 50) || "irregulärer Rumpfbeugewert",
      ],
      LS_rules: [
        (v) => !!v || "Bitte Wert eingeben",
        (v) => (v >= 0 && v <= 99) || "irreguläre Liegestützanzahl",
      ],
      SU_rules: [
        (v) => !!v || "Bitte Wert eingeben",
        (v) => (v >= 0 && v <= 99) || "irreguläre Situpsanzahl",
      ],
      SW_rules: [
        (v) => !!v || "Bitte Wert eingeben",
        (v) => (v >= 0 && v <= 800) || "irregulärer Standweitsprungwert",
      ],
      ausdauer_rules: [
        (v) => !!v || "Bitte Wert eingeben",
        (v) => (v >= 1 && v <= 30) || "irreguläre Ausdauerrundenanzahl",
      ],
      ausdauer_rest_rules: [
        (v) => !!v || "Bitte Wert eingeben",
        (v) => (v >= 1 && v <= 54) || "irreguläre Rest-Meteranzahl",
      ],
    };
  },
  created() {},
  methods: {
    addRound() {
      this.testDaten.ausdauer++;
    },
    removeRound() {
      this.testDaten.ausdauer--;
    },
    openD() {
      if (
        this.testDaten.gewicht >= 30 &&
        this.testDaten.gewicht <= 300 &&
        this.testDaten.groeße >= 50 &&
        this.testDaten.groeße <= 250 &&
        this.testDaten.sprint_D1 >= 2.0 &&
        this.testDaten.sprint_D1 <= 10.0 &&
        this.testDaten.sprint_D2 >= 2.0 &&
        this.testDaten.sprint_D2 <= 10.0 &&
        // this.testDaten.Bal_D1_6 >= 0 &&
        // this.testDaten.Bal_D1_6 <= 8 &&
        // this.testDaten.Bal_D2_6 >= 0 &&
        // this.testDaten.Bal_D2_6 <= 8 &&
        // this.testDaten.Bal_D1_4_5 >= 0 &&
        // this.testDaten.Bal_D1_4_5 <= 8 &&
        // this.testDaten.Bal_D2_4_5 >= 0 &&
        // this.testDaten.Bal_D2_4_5 <= 8 &&
        // this.testDaten.Bal_D1_3 >= 0 &&
        // this.testDaten.Bal_D1_3 <= 8 &&
        // this.testDaten.Bal_D2_3 >= 0 &&
        // this.testDaten.Bal_D2_3 <= 8 &&
        this.testDaten.SHH_D1 >= 0 &&
        this.testDaten.SHH_D1 <= 60 &&
        this.testDaten.SHH_D2 >= 0 &&
        this.testDaten.SHH_D2 <= 60 &&
        this.testDaten.RB_D1 >= -50 &&
        this.testDaten.RB_D1 <= 50 &&
        this.testDaten.RB_D2 >= -50 &&
        this.testDaten.RB_D2 <= 50 &&
        this.testDaten.LS_Anzahl >= 0 &&
        this.testDaten.LS_Anzahl <= 99 &&
        this.testDaten.SU_Anzahl >= 0 &&
        this.testDaten.SU_Anzahl <= 99 &&
        this.testDaten.SW_D1 >= 0 &&
        this.testDaten.SW_D1 <= 800 &&
        this.testDaten.SW_D2 >= 0 &&
        this.testDaten.SW_D2 <= 800 &&
        this.testDaten.ausdauer >= 1 &&
        this.testDaten.ausdauer <= 30
      )
        this.dialog = true;
      else {
        alert("Eine oder mehre Eingaben sind nicht korrekt!");
        this.dialog = false;
      }
    },
    done() {
      this.$emit("testCreated", this.testDaten);
      this.$router.push({ name: "Home" });
    },

    calculateAge(birthd) {
      let b = moment(birthd);
      return moment().diff(b, "years");
    },
  },
};
</script>

